  --image-height: calc(var(--image-width));
}
@media screen and (min-width: 768px) {
  :root {
    --image-width: calc((758px - 50px) / 3);
    --image-height: calc(var(--image-width));
  }
}
@media screen and (min-width:968px){
    :root{
        --image-width: calc((900px - 50px) / 3);
        --image-height: calc(var(--image-width));
